#root {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #40ad88;
  width: 100%;
  height: 100vh;
}

.gme-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
}

.current-player {
  display: flex;
  margin: 20px auto 0;
  justify-content: center;
  align-items: center;
}

.btn_start {
  width: 150px;
  height: 50px;
  border-radius: 10px;
  border: none;
  outline: none;
  margin: 0 auto 50px;

  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  cursor: pointer;
  background: #379274;
}

.btn_start,
.current-player {
  color: #23604c;
  font-size: 24px;
  font-family: cursive;
}

.btn_start:hover {
  background-color: #3b9e7d;
  transform: scale(1.1);
  transition: 0.3s all ease-in-out;
}