.table-item {
    width: 100%;
    height: 100%;
    background-color: #40ad88;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-family: cursive;
    color: #23604c;
}

.table-item:hover {
    background-color: #3b9e7d;
    transition: 0.3s all ease;
}